@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;1,900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  /* box-sizing: border-box; */
  margin: 0;
  padding: 0;
}


/* Font Style */

h1 {
  font-family: 'Roboto', sans-serif;
  font-weight: 900;
  font-size: 48px;
  margin-top: 45px;
  margin-bottom: 25px;
}

.h1-splash {
  font-size: 120px;
}

.h1-section-header {
  font-size: 58px;
}

h2 {
  font-family: 'Roboto', sans-serif;
}

.h2-splash {
  font-weight: 400;
  color: #858585;
  text-align: center;
}

h3 {
  font-size: 20px;
  font-weight: 400;
  text-transform: uppercase;
  padding-bottom: 125px;
}

h3::before {
  content: "";
  display: inline-block;
  width: 100px;
  border-bottom: 1px solid #000000;
  vertical-align: middle;
  margin-right: 3px;
  text-transform: uppercase;

}

h4 {
  font-size: 24px;
  font-weight: 400;
  position: relative;
  margin-bottom: 30px;
}

.highlight-box {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  position: relative;
}

.highlight {
  z-index: -1;
  width: 100%;
  position: absolute;
  height: 10px;
  border-bottom: solid #E7C82A 10px;
  bottom: -2px;
  opacity: .5;
}

h5 {
  font-size: 16px;
  font-weight: 900;
}

p {
  font-family: 'Roboto', sans-serif;
  font-weight: 300;
  line-height: 150.2%;
  font-size: 22px;
}

.bold {
  font-weight: bold;
}

.quote {
  font-weight: medium;
  font-size: 50px;
}

.overview-title {
  /* border: 1px hotpink solid; */
  max-width: 1140px;
  width: 100%;
}

/* Flex Classes */
.flex {
  display: flex;
}

.center {
  justify-content: center;
  align-items: center;
}

.flex-d-col {
  flex-direction: column;
}

.flex-d-row {
  flex-direction: row;
}

.space-between {
  justify-content: space-between;
}

.space-around {
  justify-content: space-around;
}

.center-vertical {
  align-items: center;
  align-content: center;
}

.flex-start {
  justify-content: flex-start;
}

.flex-wrap {
  flex-wrap: wrap;
}

.no-top-margin {
  margin-top: 0;
}

/* Sections */
section {
  width: 80%;
  /* border: 1px solid hotpink; */
  padding-top: 215px;
  max-width: 1140px;
}

.section-gray {
  background-color: #F6F6F6;
}

.full-bleed {
  width: 100%;
}

/* **********************  */
/* Mobile Responsiveness */
/* ********************* */

/* Smaller Desktop */
@media only screen and (max-width: 980px) {
  h3 {
    font-size: 18px;
    padding-bottom: 20px;
  }
}

/* Desktop to Tablet */
@media only screen and (max-width: 830px) {
  /* Fonts */
  h1 {
    font-size: 36px;
    margin-top: 25px;
    margin-bottom: 15px;
  }
  
  .h1-splash {
    font-size: 72px;
  }
  
  .h1-section-header {
    font-size: 42px;
  }

  .h2-splash {
    font-size: 30px;
  }
  
  h3 {
    font-size: 18px;
    padding-bottom: 60px;
  }
  
  h4 {
    font-size: 20px;
  }
  
  h5 {
    font-size: 16px;
  }
  
  p {
    line-height: 150.2%;
    font-size: 16px;
  }
  
  .quote {
    font-size: 30px;
  }

  /* Sections */
  section {
    width: 85%;
    padding-top: 115px;
  }
  
}



/* Tablet to Mobile */
@media only screen and (max-width: 550px) {
  /* Fonts */
  h1 {
    font-size: 32px;
    margin-top: 15px;
  }
  
  .h1-splash {
    font-size: 64px;
  }
  
  .h1-section-header {
    font-size: 20px;
  }

  .h2-splash {
    font-size: 20px;
  }

  h3 {
    font-size: 12px;
    padding-bottom: 30px;
  }
  
  h4 {
    font-size: 18px;
  }


  
  h5 {
    font-size: 16px;
  }
  
  p {
    line-height: 150.2%;
    font-size: 12px;
  }
  
  .quote {
    font-size: 18px;
  }

  /* Sections */
  section {
    width: 90%;
    padding-top: 75px;
  }

}
/* Navigation Bar Styling */

.nav {
    width: 100%;
    /* border: 1px green solid; */
    height: 88px;
    /* border-bottom: 1px solid gray; */
    box-shadow: 0 8px 6px -6px #DFDFDF;
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    background-color: #FFFFFF;
    z-index: 1;
}

.nav-wrapper {
    /* border: 1px solid hotpink; */
    padding: 0px;
}

.menu {
    width: 350px;
    /* border: 1px solid gray; */
}

.nav-item {
    text-decoration: none;
    color: #000000;
}

.nav-item > p {
    font-size: 20px;
    font-weight: 300;
}

.nav-item:hover {
    color: #7A9872;
    /* text-decoration: underline; */
    position: relative;
}

.nav-item:hover::before {
    content:"";
    position: absolute;
    color: #7A9872;
    width: 100%;
    height: 10px;
    bottom: 3px;
    background-color: #7A9872;
    opacity: .5;
}
.footer, .footer-wrapper {
    /* border: 1px solid green; */
    padding: 0px;
}
.footer {
    height: 288px;
    /* margin-top: 150px; */
    background-color: #EAEAEA;
}

.footer-wrapper {
    margin-top: 60px;
}

.social-media-icons > img {
    width: 48px;
}

a {
    text-decoration: none;  
    position: relative;
}

.email {
    font-size: 48px;
    font-weight: 900;
    text-decoration: none;
    color: black;
    position: relative;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
}

.email::after {
    content: "";
    position: absolute;
    width: 102%;
    height: 10px;
    border-bottom: solid #E7C82A 20px;
    opacity: .5;
    bottom: 0px;
    left: 0px;
}


/* **********************  */
/* Mobile Responsiveness */
/* ********************* */

/* Tablet */
@media only screen and (max-width: 800px) {

    .footer {
        height: 250px;
    }

    .social-media-icons > img{
        width: 30px;
    }

    .email {
        font-size: 30px;
    }
    .email::after {
        border-bottom: solid #E7C82A 10px;
    }
}

/* Mobile */
@media only screen and (max-width: 450px) {

    .footer-wraper {
        margin-top: 30px;
    }
    .footer {
        height: 150px;
    }

    .social-media-icons > img {
        width: 20px;
    }

    .email {
        font-size: 24px;
    }
}
/* Tags */

.project-card {
    width: 49%;
    /* border: 1px solid blue; */
    margin-bottom: 50px;
}

.project-tag-wrapper {
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    flex-wrap: wrap;
    margin-bottom: 15px;
}

.project-image {
    width: 100%;

}

.tag {
    background-color: #DFDFDF;
    margin-right: 10px;
    height: 40px;
    /* width: 200px;
    max-width: 200px; */
    /* padding top, right, bottom, left */
    padding: 0px, 15px, 0px, 5px;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    /* border: 1px blue solid; */
    border-radius: 10px;
    margin-bottom: 10px;
}

.h5-tag {
    font-size: 16px;
    font-weight: 700;
    text-align: center;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    padding: 15px;
    /* border: 1px hotpink solid; */
}

.project-card:nth-child(even) {
    margin-top: 180px;
}

.link {
    text-decoration: none;
    color: black;
}

.link:hover {
    color: #7A9872;
}

.link::after:hover {
    background-color: #7A9872;
}


/* **********************  */
/* Mobile Responsiveness */
/* ********************* */

/* Tablet */
@media only screen and (max-width: 800px) {

    .tag {
        margin-right: 5px;
        height: 35px;
        width: -webkit-fit-content;
        width: -moz-fit-content;
        width: fit-content;
        border-radius: 5px;
        margin-bottom: 10px;
    }

    .h5-tag {
        font-size: 13px;
        padding: 8px;
    }

    .project-card {
        width: 49%;
        /* border: 1px solid blue; */
        margin-bottom: 30px;
    }


    .project-card:nth-child(even) {
        margin-top: 100px;
    }

}

/* Inbetween Tablet & Mobile */
@media only screen and (max-width: 650px) {
    .tag {
        margin-right: 5px;
        height: 25px;
        border-radius: 3px;
        margin-bottom: 7px;
    }

    .h5-tag {
        font-size: 11px;
        padding: 8px;
    }

    .projects-row {
        flex-direction: column;
    }

    .project-card {
        width: 100%;
        display: flex;
        flex-direction: row;
        justify-content: space-between;
    }

    .project-card:nth-child(even) {
        margin-top: 100px;
    }

    .project-image-wrapper {
        width: 48%;

    }

    .project-info {
        width: 48%;
    }
}

/* Mobile */
@media only screen and (max-width: 450px) {
    .project-card {
        width: 100%;
        display: flex;
        flex-direction: column;

    }
    .project-card:nth-child(even) {
        margin-top: 60px;
    }

    .project-image-wrapper {
        width: 100%;

    }

    .project-info {
        width: 100%;
    }
}
